<template>
    <div class="p-grid">
        <div class="p-col-12 p-md-6 p-lg-4 p-md-offset-3 p-lg-offset-4 p-mt-5">
            <p-card>
                <template #title> Login </template>
                <template #content>
                    <div class="p-fluid">
                        <div class="p-field">
                            <label for="firstname">Username</label>
                            <p-input-text
                                type="text"
                                v-model="username"
                                class="p-inputtext-lg"
                                :class="{'p-invalid': invalid }"
                            />
                        </div>
                        <div class="p-field">
                            <label for="lastname">Password</label>
                            <p-password
                                v-model="password"
                                toggleMask
                                :feedback="false"
                                class="p-inputtext-lg"
                                :class="{'p-invalid': invalid }"
                            />
                        </div>
                        <p-message severity="error" v-if="invalid">Username or Password incorrect</p-message>
                    </div>
                </template>
                <template #footer>
                    <p-button
                        icon="pi pi-sign-in"
                        label="Login"
                        :loading="isLoading"
                        @click="login()"
                    />
                </template>
            </p-card>
        </div>
    </div>
</template>

<script>
import { ref } from "vue";
import router from '@/router'

export default {
    setup() {
        const username = ref();
        const password = ref();
        const isLoading = ref(false);
        const invalid = ref(false)

        const login = () => {
            isLoading.value = true;
            setTimeout(() => {
                if(username.value === 'admin' && password.value === 'admin'){
                    router.push({name: 'ArtCycle'})
                } else {
                    invalid.value = true;
                }
            }, 500);
        }

        return {
            username,
            password,
            isLoading,
            invalid,
            login
        };
    },
};
</script>
